import React from "react";
import "./Contact.css";
import { SocialIcon } from "react-social-icons";
import PropTypes from "prop-types";
import { AiFillPhone, AiFillMail } from "react-icons/ai";
import { gitHubDetails, gitLabDetails, linkedInDetails } from "../../config.js";
function Contact({ dark_mode }) {
    return (
        <div className={dark_mode ? "dark_mode_contact" : "light_mode_contact"}>
            <div className="container py-3" id="Contact" align="center">
                <div className="row">
                    <div className="col-md-6 offset-md-3 py-3 border">
                        <h2>
                            <b> Contact </b>
                        </h2>
                        <br />
                        <div>
                            <AiFillPhone />
                            <a className="link" href="tel:+30698-044-3908">
                                +30 698-044-3908
                            </a>
                            <br />
                            <AiFillMail />
                            <a className="link" href="mailto:alexiszamanidis@outlook.com">
                                alexiszamanidis@outlook.com
                            </a>
                            <br />
                        </div>
                        <br />
                        <div className="py-3">
                            <SocialIcon
                                className="social_icon"
                                url={gitHubDetails.profileLink}
                                target="_blank"
                                rel="noopener"
                            />
                            <a
                                className="gitlab"
                                href={gitLabDetails.profileLink}
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                <img
                                    className="gitlab"
                                    src="/images/GitLab.png"
                                    alt="gitlab_logo"
                                />
                            </a>
                            <SocialIcon
                                className="social_icon"
                                url={linkedInDetails.profileLink}
                                target="_blank"
                                rel="noopener"
                            />
                        </div>
                        <br />
                        <div>
                            <p>
                                <span>
                                    <i>"The most essential quality for a good team is trust"</i>
                                </span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

Contact.propTypes = {
    dark_mode: PropTypes.bool.isRequired,
};

export default Contact;
